/**
 * –°–∏—Å—Ç–µ–º–∞ —É–ø—Ä–∞–≤–ª–µ–Ω–∏—è –ø–ª–∞–≤–Ω—ã–º —Å–∫—Ä–æ–ª–ª–æ–º –Ω–∞ —Å–∞–π—Ç–µ
 * –ü–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–π –∏ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª —Å –ø–ª–∞–≤–Ω—ã–º–∏ –ø–µ—Ä–µ—Ö–æ–¥–∞–º–∏
 */

export interface ScrollSection {
  id: string;
  element: HTMLElement;
  type: 'vertical' | 'horizontal';
  index: number;
}

export class ScrollController {
  private sections: ScrollSection[] = [];
  private currentSectionIndex: number = 0;
  private isScrolling: boolean = false;
  private isHorizontallyScrolling: boolean = false; // –§–ª–∞–≥ –¥–ª—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ –≤–æ –≤—Ä–µ–º—è –∞–Ω–∏–º–∞—Ü–∏–∏ —Å–ª–∞–π–¥–æ–≤
  private scrollTimeout: NodeJS.Timeout | null = null;
  private horizontalScrollProgress: number = 0;
  private horizontalScrollMax: number = 0;
  private lastScrollTime: number = 0;
  private scrollCooldown: number = 800; // –í–æ–∑–≤—Ä–∞—â–∞–µ–º –∏—Å—Ö–æ–¥–Ω—É—é –∑–∞–¥–µ—Ä–∂–∫—É
  private savedBrandsPosition: number = 0; // –°–æ—Ö—Ä–∞–Ω—è–µ–º –ø–æ–∑–∏—Ü–∏—é –±—Ä–µ–Ω–¥–æ–≤ –ø—Ä–∏ –≤—ã—Ö–æ–¥–µ
  private hasLeftControlledArea: boolean = false; // –§–ª–∞–≥: –≤—ã—à–ª–∏ –ª–∏ –∑–∞ –ø—Ä–µ–¥–µ–ª—ã –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π
  private touchStartY: number = 0; // –ù–∞—á–∞–ª—å–Ω–∞—è –ø–æ–∑–∏—Ü–∏—è –∫–∞—Å–∞–Ω–∏—è –ø–æ Y
  private touchStartX: number = 0; // –ù–∞—á–∞–ª—å–Ω–∞—è –ø–æ–∑–∏—Ü–∏—è –∫–∞—Å–∞–Ω–∏—è –ø–æ X
  private handleBrandScrollUpdate: ((e: Event) => void) | null = null; // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü–∏–∏ —Å Brands
  private isEnabled: boolean = true; // –§–ª–∞–≥ –∞–∫—Ç–∏–≤–Ω–æ—Å—Ç–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä–∞
  private wheelThrottle: number = 0; // Throttle –¥–ª—è wheel —Å–æ–±—ã—Ç–∏–π
  private wheelThrottleDelay: number = 50; // –ó–∞–¥–µ—Ä–∂–∫–∞ throttle –≤ –º—Å
  private isMacOS: boolean = false; // –§–ª–∞–≥ –¥–ª—è macOS
  private isAppleMouse: boolean = false; // –§–ª–∞–≥ –¥–ª—è Apple Magic Mouse
  private deltaYThreshold: number = 10; // –ü–æ—Ä–æ–≥ –¥–ª—è deltaY (–∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º –º–µ–ª–∫–∏–µ –¥–≤–∏–∂–µ–Ω–∏—è)
  private isMobileDevice: boolean = false; // –§–ª–∞–≥ –º–æ–±–∏–ª—å–Ω–æ–≥–æ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞
  private resizeHandler: (() => void) | null = null; // –û–±—Ä–∞–±–æ—Ç—á–∏–∫ resize
  
  // –ö–æ–ª–±—ç–∫–∏
  private onSectionChange?: (index: number, section: ScrollSection) => void;
  private onHorizontalProgress?: (progress: number, max: number) => void;

  constructor() {
    this.handleWheel = this.handleWheel.bind(this);
    this.handleKeyDown = this.handleKeyDown.bind(this);
    this.handleTouchStart = this.handleTouchStart.bind(this);
    this.handleTouchMove = this.handleTouchMove.bind(this);
    this.checkMobileDevice = this.checkMobileDevice.bind(this);
  }

  /**
   * –ü—Ä–æ–≤–µ—Ä–∫–∞, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ –º–æ–±–∏–ª—å–Ω—ã–º
   */
  private checkMobileDevice(): boolean {
    return window.innerWidth <= 992;
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä–∞
   */
  public init(sectionIds: string[]) {
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –º–æ–±–∏–ª—å–Ω–æ–µ –ª–∏ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ
    this.isMobileDevice = this.checkMobileDevice();
    
    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º macOS
    this.isMacOS = /Mac|iPod|iPhone|iPad/.test(navigator.platform) || 
                   /Mac|iPod|iPhone|iPad/.test(navigator.userAgent);
    
    // –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –¥–ª—è macOS
    if (this.isMacOS) {
      this.scrollCooldown = 1000; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º cooldown –¥–ª—è macOS
      this.deltaYThreshold = 30; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –ø–æ—Ä–æ–≥ –¥–ª—è —Ñ–∏–ª—å—Ç—Ä–∞—Ü–∏–∏ –º–µ–ª–∫–∏—Ö –¥–≤–∏–∂–µ–Ω–∏–π
      this.wheelThrottleDelay = 100; // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º throttle
    }
    
    // –î–∏–Ω–∞–º–∏—á–µ—Å–∫–æ–µ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ Apple Magic Mouse –ø–æ —Ö–∞—Ä–∞–∫—Ç–µ—Ä—É —Å–∫—Ä–æ–ª–ª–∞
    // Magic Mouse –æ—Ç–ø—Ä–∞–≤–ª—è–µ—Ç –æ—á–µ–Ω—å –º–∞–ª–µ–Ω—å–∫–∏–µ –∑–Ω–∞—á–µ–Ω–∏—è deltaY (–æ–±—ã—á–Ω–æ 1-4)
    let wheelEventCount = 0;
    let smallDeltaCount = 0;
    const detectAppleMouse = (e: WheelEvent) => {
      if (!this.isMacOS) return;
      
      wheelEventCount++;
      if (Math.abs(e.deltaY) <= 4 && Math.abs(e.deltaY) > 0) {
        smallDeltaCount++;
      }
      
      // –ü–æ—Å–ª–µ 10 —Å–æ–±—ã—Ç–∏–π –æ–ø—Ä–µ–¥–µ–ª—è–µ–º —Ç–∏–ø –º—ã—à–∫–∏
      if (wheelEventCount === 10) {
        // –ï—Å–ª–∏ –±–æ–ª—å—à–µ 70% —Å–æ–±—ã—Ç–∏–π –∏–º–µ–ª–∏ deltaY <= 4, —ç—Ç–æ Magic Mouse
        if (smallDeltaCount >= 7) {
          this.isAppleMouse = true;
          // –ù–∞—Å—Ç—Ä–∞–∏–≤–∞–µ–º –ø–∞—Ä–∞–º–µ—Ç—Ä—ã –¥–ª—è Apple Magic Mouse
          this.scrollCooldown = 600; // –£–º–µ–Ω—å—à–∞–µ–º cooldown
          this.deltaYThreshold = 2; // –û–ß–ï–ù–¨ –Ω–∏–∑–∫–∏–π –ø–æ—Ä–æ–≥ –¥–ª—è Magic Mouse
          this.wheelThrottleDelay = 30; // –£–º–µ–Ω—å—à–∞–µ–º throttle
        }
        window.removeEventListener('wheel', detectAppleMouse);
      }
    };
    
    if (this.isMacOS) {
      window.addEventListener('wheel', detectAppleMouse, { passive: true });
    }
    
    // –†–µ–≥–∏—Å—Ç—Ä–∏—Ä—É–µ–º –≤—Å–µ —Å–µ–∫—Ü–∏–∏
    this.sections = sectionIds
      .map((id, index) => {
        const element = document.getElementById(id);
        if (!element) return null;
        
        return {
          id,
          element,
          type: id === 'brands' ? 'horizontal' : 'vertical',
          index
        } as ScrollSection;
      })
      .filter((section): section is ScrollSection => section !== null);

    // –ù–∞ –º–æ–±–∏–ª—å–Ω—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö –∏—Å–ø–æ–ª—å–∑—É–µ–º –Ω–∞—Ç–∏–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª
    if (this.isMobileDevice) {
      console.log('ScrollController: –º–æ–±–∏–ª—å–Ω–æ–µ —É—Å—Ç—Ä–æ–π—Å—Ç–≤–æ, –∫–∞—Å—Ç–æ–º–Ω—ã–π —Å–∫—Ä–æ–ª–ª –æ—Ç–∫–ª—é—á–µ–Ω');
      this.isEnabled = false;
      
      // –î–æ–±–∞–≤–ª—è–µ–º —Ç–æ–ª—å–∫–æ resize –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏ —Ä–∞–∑–º–µ—Ä–∞
      this.resizeHandler = () => {
        const wasMobile = this.isMobileDevice;
        this.isMobileDevice = this.checkMobileDevice();
        
        if (wasMobile && !this.isMobileDevice) {
          // –ü–µ—Ä–µ–∫–ª—é—á–∏–ª–∏—Å—å —Å –º–æ–±–∏–ª—å–Ω–æ–≥–æ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø
          console.log('ScrollController: –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø —Ä–µ–∂–∏–º');
          this.enableScrollControl();
        } else if (!wasMobile && this.isMobileDevice) {
          // –ü–µ—Ä–µ–∫–ª—é—á–∏–ª–∏—Å—å —Å –¥–µ—Å–∫—Ç–æ–ø–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã–π
          console.log('ScrollController: –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã–π —Ä–µ–∂–∏–º');
          this.disableScrollControl();
        }
      };
      window.addEventListener('resize', this.resizeHandler);
      return;
    }

    // –ù–∞ –¥–µ—Å–∫—Ç–æ–ø–µ –¥–æ–±–∞–≤–ª—è–µ–º –æ–±—Ä–∞–±–æ—Ç—á–∏–∫–∏ —Å–æ–±—ã—Ç–∏–π
    window.addEventListener('wheel', this.handleWheel, { passive: false });
    window.addEventListener('keydown', this.handleKeyDown);
    window.addEventListener('touchstart', this.handleTouchStart, { passive: false });
    window.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    window.addEventListener('scroll', this.handleScroll.bind(this), { passive: true });

    // –°–ª—É—à–∞–µ–º —Å–æ–±—ã—Ç–∏—è –æ—Ç Brands –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ (–∫–æ–≥–¥–∞ –∫–ª–∏–∫–∞—é—Ç –Ω–∞ —Ç–æ—á–∫–∏)
    this.handleBrandScrollUpdate = (e: Event) => {
      const customEvent = e as CustomEvent<{ index: number }>;
      const newIndex = customEvent.detail.index;
      
      // –ö–†–ò–¢–ò–ß–ù–û: –ò–≥–Ω–æ—Ä–∏—Ä—É–µ–º —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∞—Ü–∏—é, –µ—Å–ª–∏ –∏–¥–µ—Ç –∞–∫—Ç–∏–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª
      // –≠—Ç–æ –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ—Ç race condition
      if (this.isHorizontallyScrolling) {
        return;
      }
      
      // –û–±–Ω–æ–≤–ª—è–µ–º –≤–Ω—É—Ç—Ä–µ–Ω–Ω–µ–µ —Å–æ—Å—Ç–æ—è–Ω–∏–µ scrollController
      this.horizontalScrollProgress = newIndex;
      this.savedBrandsPosition = newIndex;
      
      console.log(`ScrollController —Å–∏–Ω—Ö—Ä–æ–Ω–∏–∑–∏—Ä–æ–≤–∞–Ω —Å Brands: –ø–æ–∑–∏—Ü–∏—è ${newIndex}`);
    };
    window.addEventListener('brandScrollUpdate', this.handleBrandScrollUpdate);

    // –î–æ–±–∞–≤–ª—è–µ–º resize –æ–±—Ä–∞–±–æ—Ç—á–∏–∫ –¥–ª—è –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏—è —Ä–µ–∂–∏–º–æ–≤
    this.resizeHandler = () => {
      const wasMobile = this.isMobileDevice;
      this.isMobileDevice = this.checkMobileDevice();
      
      if (wasMobile && !this.isMobileDevice) {
        // –ü–µ—Ä–µ–∫–ª—é—á–∏–ª–∏—Å—å —Å –º–æ–±–∏–ª—å–Ω–æ–≥–æ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø
        console.log('ScrollController: –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –Ω–∞ –¥–µ—Å–∫—Ç–æ–ø —Ä–µ–∂–∏–º');
        this.enableScrollControl();
      } else if (!wasMobile && this.isMobileDevice) {
        // –ü–µ—Ä–µ–∫–ª—é—á–∏–ª–∏—Å—å —Å –¥–µ—Å–∫—Ç–æ–ø–∞ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã–π
        console.log('ScrollController: –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ –Ω–∞ –º–æ–±–∏–ª—å–Ω—ã–π —Ä–µ–∂–∏–º');
        this.disableScrollControl();
      }
    };
    window.addEventListener('resize', this.resizeHandler);

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º —Ç–µ–∫—É—â—É—é —Å–µ–∫—Ü–∏—é –ø—Ä–∏ –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏–∏
    this.updateCurrentSection();
  }

  /**
   * –í–∫–ª—é—á–µ–Ω–∏–µ –∫–æ–Ω—Ç—Ä–æ–ª—è —Å–∫—Ä–æ–ª–ª–∞ (–¥–ª—è –¥–µ—Å–∫—Ç–æ–ø–∞)
   */
  private enableScrollControl() {
    if (this.isEnabled) return;
    
    this.isEnabled = true;
    window.addEventListener('wheel', this.handleWheel, { passive: false });
    window.addEventListener('keydown', this.handleKeyDown);
    window.addEventListener('touchstart', this.handleTouchStart, { passive: false });
    window.addEventListener('touchmove', this.handleTouchMove, { passive: false });
    window.addEventListener('scroll', this.handleScroll.bind(this), { passive: true });
    
    if (this.handleBrandScrollUpdate) {
      window.addEventListener('brandScrollUpdate', this.handleBrandScrollUpdate);
    }
  }

  /**
   * –û—Ç–∫–ª—é—á–µ–Ω–∏–µ –∫–æ–Ω—Ç—Ä–æ–ª—è —Å–∫—Ä–æ–ª–ª–∞ (–¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö)
   */
  private disableScrollControl() {
    if (!this.isEnabled) return;
    
    this.isEnabled = false;
    window.removeEventListener('wheel', this.handleWheel);
    window.removeEventListener('keydown', this.handleKeyDown);
    window.removeEventListener('touchstart', this.handleTouchStart);
    window.removeEventListener('touchmove', this.handleTouchMove);
    window.removeEventListener('scroll', this.handleScroll);
    
    if (this.handleBrandScrollUpdate) {
      window.removeEventListener('brandScrollUpdate', this.handleBrandScrollUpdate);
    }
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –æ–±—ã—á–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞ –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è —Ç–µ–∫—É—â–µ–π —Å–µ–∫—Ü–∏–∏
   */
  private handleScroll() {
    // –ü—Ä–æ—Å—Ç–æ –æ–±–Ω–æ–≤–ª—è–µ–º —Ç–µ–∫—É—â—É—é —Å–µ–∫—Ü–∏—é –¥–ª—è –ø—Ä–∞–≤–∏–ª—å–Ω–æ–π –ø–æ–¥—Å–≤–µ—Ç–∫–∏ –≤ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏
    // –ù–ï –≤–∫–ª—é—á–∞–µ–º –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –∞–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –ø—Ä–∏ –æ–±—ã—á–Ω–æ–º —Å–∫—Ä–æ–ª–ª–µ
    this.updateCurrentSection();
  }

  /**
   * –û—á–∏—Å—Ç–∫–∞ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä–∞
   */
  public destroy() {
    window.removeEventListener('wheel', this.handleWheel);
    window.removeEventListener('keydown', this.handleKeyDown);
    window.removeEventListener('touchstart', this.handleTouchStart);
    window.removeEventListener('touchmove', this.handleTouchMove);
    window.removeEventListener('scroll', this.handleScroll);
    
    if (this.handleBrandScrollUpdate) {
      window.removeEventListener('brandScrollUpdate', this.handleBrandScrollUpdate);
    }
    
    if (this.resizeHandler) {
      window.removeEventListener('resize', this.resizeHandler);
    }
    
    if (this.scrollTimeout) {
      clearTimeout(this.scrollTimeout);
    }
    
    // –û—á–∏—â–∞–µ–º –∫–ª–∞—Å—Å –ø–ª–∞–≤–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
    document.documentElement.classList.remove('smooth-scroll');
  }

  /**
   * –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –∫–æ–ª–±—ç–∫–∞ –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Å–µ–∫—Ü–∏–∏
   */
  public onSectionChangeCallback(callback: (index: number, section: ScrollSection) => void) {
    this.onSectionChange = callback;
  }

  /**
   * –£—Å—Ç–∞–Ω–æ–≤–∫–∞ –∫–æ–ª–±—ç–∫–∞ –¥–ª—è –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
   */
  public onHorizontalProgressCallback(callback: (progress: number, max: number) => void) {
    this.onHorizontalProgress = callback;
  }

  /**
   * –û—Ç–∫–ª—é—á–∏—Ç—å ScrollController (–Ω–∞–ø—Ä–∏–º–µ—Ä, –ø—Ä–∏ –æ—Ç–∫—Ä—ã—Ç–∏–∏ –º–æ–¥–∞–ª—å–Ω–æ–≥–æ –æ–∫–Ω–∞)
   */
  public disable() {
    this.isEnabled = false;
  }

  /**
   * –í–∫–ª—é—á–∏—Ç—å ScrollController
   */
  public enable() {
    this.isEnabled = true;
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –∫–æ–ª–µ—Å–∞ –º—ã—à–∏
   */
  private handleWheel(e: WheelEvent) {
    // –ï—Å–ª–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –æ—Ç–∫–ª—é—á–µ–Ω, –Ω–µ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º —Å–æ–±—ã—Ç–∏—è
    if (!this.isEnabled) {
      return;
    }
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–∞—Ö–æ–¥–∏–º—Å—è –ª–∏ –º—ã –≤ —Å–µ–∫—Ü–∏–∏ –±—Ä–µ–Ω–¥–æ–≤ –ü–ï–†–í–´–ú –î–ï–õ–û–ú
    const currentSection = this.sections[this.currentSectionIndex];
    
    // –ê–ì–†–ï–°–°–ò–í–ù–ê–Ø –±–ª–æ–∫–∏—Ä–æ–≤–∫–∞ –¥–ª—è —Å–µ–∫—Ü–∏–∏ –±—Ä–µ–Ω–¥–æ–≤ - –ø—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º –ø—Ä–æ—Å–∫–æ–∫
    if (currentSection && currentSection.type === 'horizontal') {
      e.preventDefault();
      e.stopPropagation();
      
      // –ï—Å–ª–∏ —É–∂–µ –∏–¥–µ—Ç —Å–∫—Ä–æ–ª–ª - –ù–ï –¥–µ–ª–∞–µ–º –Ω–∏—á–µ–≥–æ
      if (this.isScrolling || this.isHorizontallyScrolling) {
        return;
      }
      
      // –ê–¥–∞–ø—Ç–∏–≤–Ω—ã–π –ø–æ—Ä–æ–≥ –¥–ª—è —Ä–∞–∑–Ω—ã—Ö —Ç–∏–ø–æ–≤ –º—ã—à–µ–∫
      let brandsThreshold = 8; // –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é
      if (this.isAppleMouse) {
        brandsThreshold = 1; // –î–ª—è Apple Magic Mouse - –º–∏–Ω–∏–º–∞–ª—å–Ω—ã–π –ø–æ—Ä–æ–≥
      } else if (this.isMacOS) {
        brandsThreshold = 3; // –î–ª—è –æ–±—ã—á–Ω—ã—Ö –º—ã—à–µ–∫ –Ω–∞ macOS
      }
      
      // –§–∏–ª—å—Ç—Ä–∞—Ü–∏—è —Ç–æ–ª—å–∫–æ –æ—á–µ–Ω—å –º–µ–ª–∫–∏—Ö —Å–ª—É—á–∞–π–Ω—ã—Ö –¥–≤–∏–∂–µ–Ω–∏–π
      if (Math.abs(e.deltaY) < brandsThreshold) {
        return;
      }
      
      // Throttle –¥–ª—è wheel —Å–æ–±—ã—Ç–∏–π
      const now = Date.now();
      
      // –ê–¥–∞–ø—Ç–∏–≤–Ω—ã–π cooldown –¥–ª—è —Ä–∞–∑–Ω—ã—Ö —Ç–∏–ø–æ–≤ –º—ã—à–µ–∫
      let brandsCooldown = 500; // –ü–æ —É–º–æ–ª—á–∞–Ω–∏—é
      if (this.isAppleMouse) {
        brandsCooldown = 200; // –î–ª—è Apple Magic Mouse - –±—ã—Å—Ç—Ä–µ–µ
      } else if (this.isMacOS) {
        brandsCooldown = 250; // –î–ª—è –æ–±—ã—á–Ω—ã—Ö –º—ã—à–µ–∫ –Ω–∞ macOS
      }
      
      // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ cooldown
      if (now - this.lastScrollTime < brandsCooldown) {
        return;
      }
      
      this.wheelThrottle = now;
      const scrollDown = e.deltaY > 0;
      
      this.handleBrandsScroll(scrollDown);
      return;
    }
    
    // –ö–†–ò–¢–ò–ß–ù–û: –ë–ª–æ–∫–∏—Ä–æ–≤–∫–∞ –¥–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Å–µ–∫—Ü–∏–π
    if (this.isScrolling || this.isHorizontallyScrolling) {
      e.preventDefault();
      return;
    }
    
    // Throttle –¥–ª—è wheel —Å–æ–±—ã—Ç–∏–π - –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏—è –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏
    const now = Date.now();
    if (now - this.wheelThrottle < this.wheelThrottleDelay) {
      return;
    }
    this.wheelThrottle = now;
    
    const scrollDown = e.deltaY > 0;
    
    // –§–∏–ª—å—Ç—Ä–∞—Ü–∏—è –º–µ–ª–∫–∏—Ö –¥–≤–∏–∂–µ–Ω–∏–π –¥–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Å–µ–∫—Ü–∏–π
    if (Math.abs(e.deltaY) < this.deltaYThreshold) {
      return;
    }
    
    // –ï—Å–ª–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –≤—ã–∫–ª—é—á–µ–Ω, –ø—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–∞—Ö–æ–¥–∏–º—Å—è –ª–∏ –º—ã –≤ –ø—Ä–µ–¥–µ–ª–∞—Ö Timeline
    if (this.hasLeftControlledArea) {
      const lastSection = this.sections[this.sections.length - 1]; // Timeline
      if (lastSection) {
        const rect = lastSection.element.getBoundingClientRect();
        const viewportHeight = window.innerHeight;
        
        // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –Ω–∞—Ö–æ–¥–∏—Ç—Å—è –ª–∏ Timeline –≤ –≤–∏–¥–∏–º–æ–π –æ–±–ª–∞—Å—Ç–∏
        const timelineVisible = rect.top < viewportHeight && rect.bottom > 0;
        const timelineVisibleHeight = Math.min(rect.bottom, viewportHeight) - Math.max(rect.top, 0);
        const timelineVisibilityRatio = timelineVisibleHeight / viewportHeight;
        
        // –ï—Å–ª–∏ Timeline –∑–∞–Ω–∏–º–∞–µ—Ç 50%+ —ç–∫—Ä–∞–Ω–∞, –≤–∫–ª—é—á–∞–µ–º –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –æ–±—Ä–∞—Ç–Ω–æ
        if (timelineVisible && timelineVisibilityRatio >= 0.5) {
          this.hasLeftControlledArea = false;
          document.documentElement.classList.remove('smooth-scroll');
          this.currentSectionIndex = this.sections.length - 1;
          // –ù–ï –≤–æ–∑–≤—Ä–∞—â–∞–µ–º—Å—è –∏–∑ —Ñ—É–Ω–∫—Ü–∏–∏ ‚Äî –ø—Ä–æ–¥–æ–ª–∂–∞–µ–º –æ–±—Ä–∞–±–æ—Ç–∫—É —Å–∫—Ä–æ–ª–ª–∞
        } else {
          // –í—Å–µ –µ—â–µ –≤–Ω–µ Timeline, –ø—Ä–æ–ø—É—Å–∫–∞–µ–º —Å–æ–±—ã—Ç–∏–µ
          return;
        }
      } else {
        return;
      }
    }
    
    // –ï—Å–ª–∏ –º—ã –Ω–∞ –ø–æ—Å–ª–µ–¥–Ω–µ–π –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º–æ–π —Å–µ–∫—Ü–∏–∏ (Timeline) –∏ —Å–∫—Ä–æ–ª–ª–∏–º –≤–Ω–∏–∑
    // –£—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Ñ–ª–∞–≥ –∏ —Ä–∞–∑—Ä–µ—à–∞–µ–º –æ–±—ã—á–Ω—ã–π –±—Ä–∞—É–∑–µ—Ä–Ω—ã–π —Å–∫—Ä–æ–ª–ª
    if (this.currentSectionIndex === this.sections.length - 1 && scrollDown) {
      this.hasLeftControlledArea = true;
      document.documentElement.classList.add('smooth-scroll');
      return;
    }
    
    // –ï—Å–ª–∏ –º—ã –∑–∞ –ø—Ä–µ–¥–µ–ª–∞–º–∏ –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π
    if (!currentSection || this.currentSectionIndex >= this.sections.length) {
      return;
    }

    e.preventDefault();

    // –ü—Ä–æ–≤–µ—Ä–∫–∞ –Ω–∞ cooldown –¥–ª—è —É–º–µ–Ω—å—à–µ–Ω–∏—è —á—É–≤—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ (–∏—Å–ø–æ–ª—å–∑—É–µ–º now –∏–∑ throttle)
    if (now - this.lastScrollTime < this.scrollCooldown) {
      return;
    }

    if (this.isScrolling) return;
    
    // –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª –¥–ª—è –≤—Å–µ—Ö –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Å–µ–∫—Ü–∏–π (–±—Ä–µ–Ω–¥—ã —É–∂–µ –æ–±—Ä–∞–±–æ—Ç–∞–Ω—ã –≤—ã—à–µ)
    this.handleVerticalScroll(scrollDown);
    }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ —Å–∫—Ä–æ–ª–ª–∞ –≤ —Å–µ–∫—Ü–∏–∏ –±—Ä–µ–Ω–¥–æ–≤ (–≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–ª–∞–π–¥–µ—Ä)
   * –ü–†–ê–í–ò–õ–ê:
   * 1. –ù–∞ —Å–ª–∞–π–¥–µ 0 —Å–∫—Ä–æ–ª–ª –≤–≤–µ—Ä—Ö -> –≤—ã—Ö–æ–¥ –∫ –ø—Ä–µ–¥—ã–¥—É—â–µ–π —Å–µ–∫—Ü–∏–∏
   * 2. –ù–∞ —Å–ª–∞–π–¥–µ 5 (–ø–æ—Å–ª–µ–¥–Ω–∏–π) —Å–∫—Ä–æ–ª–ª –≤–Ω–∏–∑ -> –≤—ã—Ö–æ–¥ –∫ —Å–ª–µ–¥—É—é—â–µ–π —Å–µ–∫—Ü–∏–∏
   * 3. –í–û –í–°–ï–• –û–°–¢–ê–õ–¨–ù–´–• –°–õ–£–ß–ê–Ø–• -> —Ç–æ–ª—å–∫–æ –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤, –≤—ã—Ö–æ–¥ –ó–ê–ü–†–ï–©–ï–ù
   */
  private handleBrandsScroll(scrollDown: boolean) {
    // –ü–æ–ª—É—á–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å–ª–∞–π–¥–æ–≤ (6 –±—Ä–µ–Ω–¥–æ–≤ = –∏–Ω–¥–µ–∫—Å—ã 0-5)
    const totalSlides = 6;
    const currentSlide = this.horizontalScrollProgress;
    
    // –ü—Ä–∞–≤–∏–ª–æ 1: –ù–∞ –ø–µ—Ä–≤–æ–º —Å–ª–∞–π–¥–µ (0) —Å–∫—Ä–æ–ª–ª –≤–≤–µ—Ä—Ö -> –≤—ã—Ö–æ–¥ –≤–≤–µ—Ä—Ö
    if (currentSlide === 0 && !scrollDown) {
      this.handleVerticalScroll(false);
      return;
    }
    
    // –ü—Ä–∞–≤–∏–ª–æ 2: –ù–∞ –ø–æ—Å–ª–µ–¥–Ω–µ–º —Å–ª–∞–π–¥–µ (5) —Å–∫—Ä–æ–ª–ª –≤–Ω–∏–∑ -> –≤—ã—Ö–æ–¥ –≤–Ω–∏–∑
    if (currentSlide === (totalSlides - 1) && scrollDown) {
      this.handleVerticalScroll(true);
      return;
    }
    
    // –ü—Ä–∞–≤–∏–ª–æ 3: –í—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ —Å–ª—É—á–∞–∏ -> –¢–û–õ–¨–ö–û –ø–µ—Ä–µ–∫–ª—é—á–µ–Ω–∏–µ —Å–ª–∞–π–¥–æ–≤
    // –ë–ª–æ–∫–∏—Ä—É–µ–º –ª—é–±—ã–µ –¥–∞–ª—å–Ω–µ–π—à–∏–µ —Å–æ–±—ã—Ç–∏—è –Ω–∞ –≤—Ä–µ–º—è –∞–Ω–∏–º–∞—Ü–∏–∏
    this.isHorizontallyScrolling = true;
    this.lastScrollTime = Date.now();
    
    setTimeout(() => {
      this.isHorizontallyScrolling = false;
    }, 1000);
    
    // –ü–µ—Ä–µ–∫–ª—é—á–∞–µ–º —Å–ª–∞–π–¥
    if (scrollDown && currentSlide < (totalSlides - 1)) {
      this.horizontalScrollProgress++;
      this.updateHorizontalScroll();
      
      if (this.onHorizontalProgress) {
        this.onHorizontalProgress(this.horizontalScrollProgress, totalSlides - 1);
      }
    } else if (!scrollDown && currentSlide > 0) {
      this.horizontalScrollProgress--;
      this.updateHorizontalScroll();
      
      if (this.onHorizontalProgress) {
        this.onHorizontalProgress(this.horizontalScrollProgress, totalSlides - 1);
      }
    }
  }

  /**
   * –ü—É–±–ª–∏—á–Ω—ã–µ –º–µ—Ç–æ–¥—ã –¥–ª—è –±–ª–æ–∫–∏—Ä–æ–≤–∫–∏/—Ä–∞–∑–±–ª–æ–∫–∏—Ä–æ–≤–∫–∏ —Å–∫—Ä–æ–ª–ª–∞ –∏–∑–≤–Ω–µ (–Ω–∞–ø—Ä–∏–º–µ—Ä, –∏–∑ Brands.tsx)
   */
  public lockHorizontalScroll() {
    this.isHorizontallyScrolling = true;
  }

  public unlockHorizontalScroll() {
    this.isHorizontallyScrolling = false;
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –≤–µ—Ä—Ç–∏–∫–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
   */
  private handleVerticalScroll(scrollDown: boolean) {
    this.lastScrollTime = Date.now();
    
    if (scrollDown) {
      // –°–∫—Ä–æ–ª–ª –≤–Ω–∏–∑
      if (this.currentSectionIndex < this.sections.length - 1) {
        this.scrollToSection(this.currentSectionIndex + 1);
      } else {
        // –î–æ—Å—Ç–∏–≥–ª–∏ –ø–æ—Å–ª–µ–¥–Ω–µ–π –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º–æ–π —Å–µ–∫—Ü–∏–∏ - —Ä–∞–∑—Ä–µ—à–∞–µ–º –æ–±—ã—á–Ω—ã–π —Å–∫—Ä–æ–ª–ª
        // –ë–æ–ª—å—à–µ –Ω–µ –±–ª–æ–∫–∏—Ä—É–µ–º
        return;
      }
    } else {
      // –°–∫—Ä–æ–ª–ª –≤–≤–µ—Ä—Ö - –£–ü–†–û–©–ï–ù–ù–ê–Ø –õ–û–ì–ò–ö–ê –±–µ–∑ –ø—Ä–æ–≤–µ—Ä–∫–∏ –ø–æ–∑–∏—Ü–∏–∏
      // –ü—Ä–æ—Å—Ç–æ –≤—Å–µ–≥–¥–∞ –ø–µ—Ä–µ—Ö–æ–¥–∏–º –∫ –ø—Ä–µ–¥—ã–¥—É—â–µ–π —Å–µ–∫—Ü–∏–∏
      if (this.currentSectionIndex > 0) {
        this.scrollToSection(this.currentSectionIndex - 1);
      }
    }
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞ (–¥–ª—è —Å–µ–∫—Ü–∏–∏ Brands)
   */
  private handleHorizontalScroll(scrollDown: boolean) {
    const brandsSection = this.sections[this.currentSectionIndex];
    if (!brandsSection || brandsSection.type !== 'horizontal') return;

    // –ë–ª–æ–∫–∏—Ä–æ–≤–∫–∞ —É—Å—Ç–∞–Ω–æ–≤–ª–µ–Ω–∞ –≤ handleWheel, –∑–¥–µ—Å—å —Ç–æ–ª—å–∫–æ –ø–µ—Ä–µ–∫–ª—é—á–∞–µ–º —Å–ª–∞–π–¥

    if (scrollDown) {
      // –°–∫—Ä–æ–ª–ª –≤–ø—Ä–∞–≤–æ (–≤–Ω–∏–∑ –∫–æ–ª–µ—Å–æ–º)
      if (this.horizontalScrollProgress < this.horizontalScrollMax) {
        this.horizontalScrollProgress++;
        this.updateHorizontalScroll();
        
        if (this.onHorizontalProgress) {
          this.onHorizontalProgress(this.horizontalScrollProgress, this.horizontalScrollMax);
        }
      }
    } else {
      // –°–∫—Ä–æ–ª–ª –≤–ª–µ–≤–æ (–≤–≤–µ—Ä—Ö –∫–æ–ª–µ—Å–æ–º)
      if (this.horizontalScrollProgress > 0) {
        this.horizontalScrollProgress--;
        this.updateHorizontalScroll();
        
        if (this.onHorizontalProgress) {
          this.onHorizontalProgress(this.horizontalScrollProgress, this.horizontalScrollMax);
        }
      }
    }
  }

  /**
   * –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ –ø–æ–∑–∏—Ü–∏–∏ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
   */
  private updateHorizontalScroll() {
    // –¢—Ä–∏–≥–≥–µ—Ä–∏–º —Å–æ–±—ã—Ç–∏–µ –¥–ª—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ Brands
    const event = new CustomEvent('brandScrollUpdate', {
      detail: { index: this.horizontalScrollProgress }
    });
    window.dispatchEvent(event);
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –∫–ª–∞–≤–∏—à –∫–ª–∞–≤–∏–∞—Ç—É—Ä—ã
   */
  private handleKeyDown(e: KeyboardEvent) {
    // –ï—Å–ª–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –æ—Ç–∫–ª—é—á–µ–Ω, –Ω–µ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º —Å–æ–±—ã—Ç–∏—è
    if (!this.isEnabled) {
      return;
    }
    
    if (this.isScrolling) return;

    const currentSection = this.sections[this.currentSectionIndex];
    if (!currentSection) return;

    switch (e.key) {
      case 'ArrowDown':
      case 'PageDown':
        e.preventDefault();
        if (currentSection.type === 'horizontal') {
          this.handleHorizontalScroll(true);
        } else {
          this.handleVerticalScroll(true);
        }
        break;
      
      case 'ArrowUp':
      case 'PageUp':
        e.preventDefault();
        if (currentSection.type === 'horizontal') {
          this.handleHorizontalScroll(false);
        } else {
          this.handleVerticalScroll(false);
        }
        break;
      
      case 'ArrowRight':
        if (currentSection.type === 'horizontal') {
          e.preventDefault();
          this.handleHorizontalScroll(true);
        }
        break;
      
      case 'ArrowLeft':
        if (currentSection.type === 'horizontal') {
          e.preventDefault();
          this.handleHorizontalScroll(false);
        }
        break;
      
      case 'Home':
        e.preventDefault();
        this.scrollToSection(0);
        break;
      
      case 'End':
        e.preventDefault();
        this.scrollToSection(this.sections.length - 1);
        break;
    }
  }

  /**
   * –û–±—Ä–∞–±–æ—Ç–∫–∞ –Ω–∞—á–∞–ª–∞ –∫–∞—Å–∞–Ω–∏—è –¥–ª—è touch —É—Å—Ç—Ä–æ–π—Å—Ç–≤
   */
  private handleTouchStart(e: TouchEvent) {
    // –ï—Å–ª–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –æ—Ç–∫–ª—é—á–µ–Ω, –Ω–µ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º —Å–æ–±—ã—Ç–∏—è
    if (!this.isEnabled) {
      return;
    }
    
    this.touchStartY = e.touches[0].clientY;
    this.touchStartX = e.touches[0].clientX;
  }

  private handleTouchMove(e: TouchEvent) {
    // –ï—Å–ª–∏ –∫–æ–Ω—Ç—Ä–æ–ª–ª–µ—Ä –æ—Ç–∫–ª—é—á–µ–Ω, –Ω–µ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞–µ–º —Å–æ–±—ã—Ç–∏—è
    if (!this.isEnabled) {
      return;
    }
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –≤—ã—à–ª–∏ –ª–∏ –º—ã –∑–∞ –ø—Ä–µ–¥–µ–ª—ã –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π
    if (this.hasLeftControlledArea) {
      return; // –ü–æ–∑–≤–æ–ª—è–µ–º –Ω–∞—Ç–∏–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª
    }
    
    if (this.isScrolling) {
      e.preventDefault();
      return;
    }

    const touchEndY = e.touches[0].clientY;
    const touchEndX = e.touches[0].clientX;
    const diffY = this.touchStartY - touchEndY;
    const diffX = this.touchStartX - touchEndX;

    const currentSection = this.sections[this.currentSectionIndex];
    if (!currentSection) return;

    // –£–≤–µ–ª–∏—á–∏–≤–∞–µ–º –ø–æ—Ä–æ–≥ —á—É–≤—Å—Ç–≤–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏ –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö (–±–æ–ª–µ–µ —Å—Ç—Ä–æ–≥–æ–µ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏–µ –Ω–∞–º–µ—Ä–µ–Ω–∏—è)
    const swipeThreshold = 60;

    // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –Ω–∞–ø—Ä–∞–≤–ª–µ–Ω–∏–µ —Å–≤–∞–π–ø–∞
    if (Math.abs(diffY) > Math.abs(diffX) && Math.abs(diffY) > swipeThreshold) {
      // –í–µ—Ä—Ç–∏–∫–∞–ª—å–Ω—ã–π —Å–≤–∞–π–ø
      e.preventDefault();
      if (currentSection.type === 'horizontal') {
        this.handleHorizontalScroll(diffY > 0);
      } else {
        this.handleVerticalScroll(diffY > 0);
      }
      this.touchStartY = touchEndY;
    } else if (currentSection.type === 'horizontal' && Math.abs(diffX) > swipeThreshold) {
      // –ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–≤–∞–π–ø –¥–ª—è –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–π —Å–µ–∫—Ü–∏–∏
      e.preventDefault();
      this.handleHorizontalScroll(diffX > 0);
      this.touchStartX = touchEndX;
    }
  }

  /**
   * –ü–ª–∞–≤–Ω–∞—è –∞–Ω–∏–º–∞—Ü–∏—è —Å–∫—Ä–æ–ª–ª–∞ —Å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ–º –∫—Ä–∏–≤–æ–π cubic-bezier
   * –ê–Ω–∞–ª–æ–≥–∏—á–Ω–æ —Ç–æ–º—É, –∫–∞–∫ —Ä–∞–±–æ—Ç–∞–µ—Ç —Å–ª–∞–π–¥–µ—Ä –±—Ä–µ–Ω–¥–æ–≤
   */
  private animateScroll(targetPosition: number, duration: number = 800) {
    const startPosition = window.scrollY;
    const distance = targetPosition - startPosition;
    let startTime: number | null = null;

    // Cubic bezier easing function (0.65, 0, 0.35, 1) - –∫–∞–∫ –≤ —Å–ª–∞–π–¥–µ—Ä–µ –±—Ä–µ–Ω–¥–æ–≤
    // –≠—Ç–æ —É–ª—É—á—à–µ–Ω–Ω–∞—è –≤–µ—Ä—Å–∏—è –¥–ª—è –±–æ–ª–µ–µ —Ç–æ—á–Ω–æ–≥–æ —Å–æ–æ—Ç–≤–µ—Ç—Å—Ç–≤–∏—è CSS cubic-bezier
    const easeOutQuart = (t: number): number => {
      // –ü—Ä–∏–±–ª–∏–∂–µ–Ω–∏–µ –¥–ª—è cubic-bezier(0.65, 0, 0.35, 1)
      // –°–æ–∑–¥–∞–µ—Ç –ø–ª–∞–≤–Ω–æ–µ —É—Å–∫–æ—Ä–µ–Ω–∏–µ –≤ –Ω–∞—á–∞–ª–µ –∏ –∑–∞–º–µ–¥–ª–µ–Ω–∏–µ –≤ –∫–æ–Ω—Ü–µ
      return 1 - Math.pow(1 - t, 4);
    };

    const animation = (currentTime: number) => {
      if (startTime === null) startTime = currentTime;
      const timeElapsed = currentTime - startTime;
      const progress = Math.min(timeElapsed / duration, 1);

      // –ü—Ä–∏–º–µ–Ω—è–µ–º easing
      const easedProgress = easeOutQuart(progress);
      
      // –í—ã—á–∏—Å–ª—è–µ–º —Ç–µ–∫—É—â—É—é –ø–æ–∑–∏—Ü–∏—é
      const currentPosition = startPosition + distance * easedProgress;
      
      window.scrollTo(0, currentPosition);

      if (progress < 1) {
        requestAnimationFrame(animation);
      } else {
        // –ê–Ω–∏–º–∞—Ü–∏—è –∑–∞–≤–µ—Ä—à–µ–Ω–∞
        this.isScrolling = false;
        
        // –ï—Å–ª–∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º –ö –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–π —Å–µ–∫—Ü–∏–∏ (brands)
        const section = this.sections[this.currentSectionIndex];
        if (section && section.type === 'horizontal') {
          // –í–æ—Å—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º —Å–æ—Ö—Ä–∞–Ω–µ–Ω–Ω—É—é –ø–æ–∑–∏—Ü–∏—é
          this.horizontalScrollProgress = this.savedBrandsPosition;
          this.updateHorizontalScroll();
          
          if (this.onHorizontalProgress) {
            this.onHorizontalProgress(this.horizontalScrollProgress, this.horizontalScrollMax);
          }
        }
      }
    };

    requestAnimationFrame(animation);
  }

  /**
   * –ü—Ä–∏–Ω—É–¥–∏—Ç–µ–ª—å–Ω—ã–π –≤—ã—Ö–æ–¥ –∏–∑ –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º–æ–π –æ–±–ª–∞—Å—Ç–∏
   * –ò—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –ø—Ä–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –∫ —Å–µ–∫—Ü–∏—è–º –Ω–∏–∂–µ Timeline
   */
  public leaveControlledArea() {
    this.hasLeftControlledArea = true;
    document.documentElement.classList.add('smooth-scroll');
  }

  /**
   * –°–∫—Ä–æ–ª–ª –∫ –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–Ω–æ–π —Å–µ–∫—Ü–∏–∏
   */
  public scrollToSection(index: number, smooth: boolean = true) {
    if (index < 0 || index >= this.sections.length || this.isScrolling) return;

    const section = this.sections[index];
    if (!section) return;

    this.isScrolling = true;
    
    // –ü—Ä–∏ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –∫ –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º–æ–π —Å–µ–∫—Ü–∏–∏ –≤—Å–µ–≥–¥–∞ –≤–æ–∑–≤—Ä–∞—â–∞–µ–º –∫–æ–Ω—Ç—Ä–æ–ª—å
    if (this.hasLeftControlledArea) {
      document.documentElement.classList.remove('smooth-scroll');
    }
    this.hasLeftControlledArea = false;
    
    // –ü—Ä–æ–≤–µ—Ä—è–µ–º, –≤—ã—Ö–æ–¥–∏–º –ª–∏ –º—ã –∏–∑ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–π —Å–µ–∫—Ü–∏–∏
    const currentSection = this.sections[this.currentSectionIndex];
    
    // –ï—Å–ª–∏ –≤—ã—Ö–æ–¥–∏–º –∏–∑ –±—Ä–µ–Ω–¥–æ–≤, —Å–æ—Ö—Ä–∞–Ω—è–µ–º —Ç–µ–∫—É—â—É—é –ø–æ–∑–∏—Ü–∏—é
    if (currentSection && currentSection.type === 'horizontal' && section.type !== 'horizontal') {
      this.savedBrandsPosition = this.horizontalScrollProgress;
    }
    
    this.currentSectionIndex = index;

    // –í—ã–∑—ã–≤–∞–µ–º –∫–æ–ª–±—ç–∫
    if (this.onSectionChange) {
      this.onSectionChange(index, section);
    }

    let targetPosition: number;

    // –ï—Å–ª–∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º –∫ Hero (–ø–µ—Ä–≤–∞—è —Å–µ–∫—Ü–∏—è), —Å–∫—Ä–æ–ª–ª–∏–º –¥–æ —Å–∞–º–æ–≥–æ –≤–µ—Ä—Ö–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—ã
    if (section.id === 'home') {
      targetPosition = 0;
    } else {
      // –î–ª—è –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π –∏—Å–ø–æ–ª—å–∑—É–µ–º —Ç–æ—á–Ω–æ–µ –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏–µ
      const rect = section.element.getBoundingClientRect();
      const absoluteTop = window.scrollY + rect.top;
      
      // –ü—Ä–æ–≤–µ—Ä—è–µ–º, —è–≤–ª—è–µ—Ç—Å—è –ª–∏ —Å–µ–∫—Ü–∏—è –ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω–æ–π (100vh)
      const isFullscreen = rect.height >= window.innerHeight * 0.95;
      
      if (isFullscreen) {
        // –î–ª—è –ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω—ã—Ö —Å–µ–∫—Ü–∏–π - –∫ –Ω–∞—á–∞–ª—É
        targetPosition = absoluteTop;
      } else {
        // –î–ª—è –Ω–µ–ø–æ–ª–Ω–æ—ç–∫—Ä–∞–Ω–Ω—ã—Ö —Å–µ–∫—Ü–∏–π - —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ
        const offset = (window.innerHeight - rect.height) / 2;
        targetPosition = absoluteTop - offset;
      }
    }

    if (smooth) {
      // –ò—Å–ø–æ–ª—å–∑—É–µ–º –ø–ª–∞–≤–Ω—É—é –∞–Ω–∏–º–∞—Ü–∏—é —Å —Ç–æ–π –∂–µ –∫—Ä–∏–≤–æ–π, —á—Ç–æ –≤ —Å–ª–∞–π–¥–µ—Ä–µ –±—Ä–µ–Ω–¥–æ–≤
      this.animateScroll(targetPosition, 800);
    } else {
      // –ú–≥–Ω–æ–≤–µ–Ω–Ω—ã–π –ø–µ—Ä–µ—Ö–æ–¥
      window.scrollTo(0, targetPosition);
      this.isScrolling = false;
      
      // –ï—Å–ª–∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º –ö –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–π —Å–µ–∫—Ü–∏–∏ (brands)
      if (section.type === 'horizontal') {
        this.horizontalScrollProgress = this.savedBrandsPosition;
        this.updateHorizontalScroll();
        
        if (this.onHorizontalProgress) {
          this.onHorizontalProgress(this.horizontalScrollProgress, this.horizontalScrollMax);
        }
      }
    }
  }

  /**
   * –û–±–Ω–æ–≤–ª–µ–Ω–∏–µ —Ç–µ–∫—É—â–µ–π —Å–µ–∫—Ü–∏–∏ –Ω–∞ –æ—Å–Ω–æ–≤–µ –ø–æ–∑–∏—Ü–∏–∏ —Å–∫—Ä–æ–ª–ª–∞
   */
  private updateCurrentSection() {
    // –ï—Å–ª–∏ –º—ã –≤—ã—à–ª–∏ –∏–∑ –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º–æ–π –æ–±–ª–∞—Å—Ç–∏, –ù–ï –æ–±–Ω–æ–≤–ª—è–µ–º –∏–Ω–¥–µ–∫—Å
    if (this.hasLeftControlledArea) {
      return;
    }
    
    const scrollPosition = window.scrollY + window.innerHeight / 2;
    
    for (let i = 0; i < this.sections.length; i++) {
      const section = this.sections[i];
      const rect = section.element.getBoundingClientRect();
      const sectionTop = window.scrollY + rect.top;
      const sectionBottom = sectionTop + rect.height;
      
      if (scrollPosition >= sectionTop && scrollPosition < sectionBottom) {
        this.currentSectionIndex = i;
        break;
      }
    }
  }

  /**
   * –ü–æ–ª—É—á–µ–Ω–∏–µ —Ç–µ–∫—É—â–µ–π —Å–µ–∫—Ü–∏–∏
   */
  public getCurrentSection(): ScrollSection | null {
    return this.sections[this.currentSectionIndex] || null;
  }

  /**
   * –ü–æ–ª—É—á–µ–Ω–∏–µ –∏–Ω–¥–µ–∫—Å–∞ —Ç–µ–∫—É—â–µ–π —Å–µ–∫—Ü–∏–∏
   */
  public getCurrentSectionIndex(): number {
    return this.currentSectionIndex;
  }
}

export const scrollController = new ScrollController();


