import { useState } from 'react'
import { motion } from 'framer-motion'
import { Mail, Phone, Send, User, Building, MapPin, MessageSquare, Sparkles } from 'lucide-react'
import './Contact.css'

interface FormErrors {
  name?: string
  email?: string
  company?: string
  message?: string
  privacy?: string
}

const Contact = () => {
  const [formData, setFormData] = useState({
    name: '',
    email: '',
    company: '',
    message: '',
    honeypot: '' // –°–∫—Ä—ã—Ç–æ–µ –ø–æ–ª–µ –¥–ª—è –±–æ—Ç–æ–≤
  })

  const [errors, setErrors] = useState<FormErrors>({})
  const [touched, setTouched] = useState<Record<string, boolean>>({})
  const [isSubmitting, setIsSubmitting] = useState(false)
  const [isSubmitted, setIsSubmitted] = useState(false)
  const [focusedField, setFocusedField] = useState<string | null>(null)
  const [isPrivacyAccepted, setIsPrivacyAccepted] = useState(false)
  const [lastSubmitTime, setLastSubmitTime] = useState<number>(0)

  // –§—É–Ω–∫—Ü–∏—è –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –æ—Ç–¥–µ–ª—å–Ω–æ–≥–æ –ø–æ–ª—è
  const validateField = (name: string, value: string): string | undefined => {
    switch (name) {
      case 'name':
        if (!value.trim()) {
          return '–ò–º—è –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'
        }
        if (value.trim().length < 2) {
          return '–ò–º—è –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 2 —Å–∏–º–≤–æ–ª–∞'
        }
        if (value.trim().length > 50) {
          return '–ò–º—è –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –¥–ª–∏–Ω–Ω–µ–µ 50 —Å–∏–º–≤–æ–ª–æ–≤'
        }
        if (!/^[–∞-—è–ê-–Ø—ë–Åa-zA-Z\s-]+$/.test(value)) {
          return '–ò–º—è –º–æ–∂–µ—Ç —Å–æ–¥–µ—Ä–∂–∞—Ç—å —Ç–æ–ª—å–∫–æ –±—É–∫–≤—ã, –ø—Ä–æ–±–µ–ª—ã –∏ –¥–µ—Ñ–∏—Å—ã'
        }
        break
      
      case 'email':
        if (!value.trim()) {
          return 'Email –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'
        }
        const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/
        if (!emailRegex.test(value)) {
          return '–í–≤–µ–¥–∏—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–π email –∞–¥—Ä–µ—Å'
        }
        break
      
      case 'message':
        if (!value.trim()) {
          return '–°–æ–æ–±—â–µ–Ω–∏–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ –¥–ª—è –∑–∞–ø–æ–ª–Ω–µ–Ω–∏—è'
        }
        if (value.trim().length < 10) {
          return '–°–æ–æ–±—â–µ–Ω–∏–µ –¥–æ–ª–∂–Ω–æ —Å–æ–¥–µ—Ä–∂–∞—Ç—å –º–∏–Ω–∏–º—É–º 10 —Å–∏–º–≤–æ–ª–æ–≤'
        }
        if (value.trim().length > 1000) {
          return '–°–æ–æ–±—â–µ–Ω–∏–µ –Ω–µ –º–æ–∂–µ—Ç –±—ã—Ç—å –¥–ª–∏–Ω–Ω–µ–µ 1000 —Å–∏–º–≤–æ–ª–æ–≤'
        }
        break
      
      default:
        return undefined
    }
    return undefined
  }

  // –í–∞–ª–∏–¥–∞—Ü–∏—è –≤—Å–µ–π —Ñ–æ—Ä–º—ã
  const validateForm = (): boolean => {
    const newErrors: FormErrors = {}
    
    newErrors.name = validateField('name', formData.name)
    newErrors.email = validateField('email', formData.email)
    newErrors.message = validateField('message', formData.message)
    
    if (!isPrivacyAccepted) {
      newErrors.privacy = '–ù–µ–æ–±—Ö–æ–¥–∏–º–æ —Å–æ–≥–ª–∞—Å–∏–µ –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö'
    }
    
    setErrors(newErrors)
    
    // –í–æ–∑–≤—Ä–∞—â–∞–µ–º true –µ—Å–ª–∏ –Ω–µ—Ç –æ—à–∏–±–æ–∫
    return !Object.values(newErrors).some(error => error !== undefined)
  }

  const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setFormData(prev => ({ ...prev, [name]: value }))
    
    // –í–∞–ª–∏–¥–∞—Ü–∏—è –ø—Ä–∏ –∏–∑–º–µ–Ω–µ–Ω–∏–∏, –µ—Å–ª–∏ –ø–æ–ª–µ —É–∂–µ –±—ã–ª–æ "—Ç—Ä–æ–Ω—É—Ç–æ"
    if (touched[name]) {
      const error = validateField(name, value)
      setErrors(prev => ({ ...prev, [name]: error }))
    }
  }

  const handleBlur = (e: React.FocusEvent<HTMLInputElement | HTMLTextAreaElement>) => {
    const { name, value } = e.target
    setTouched(prev => ({ ...prev, [name]: true }))
    
    // –í–∞–ª–∏–¥–∞—Ü–∏—è –ø—Ä–∏ –ø–æ—Ç–µ—Ä–µ —Ñ–æ–∫—É—Å–∞
    const error = validateField(name, value)
    setErrors(prev => ({ ...prev, [name]: error }))
    setFocusedField(null)
  }

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()
    
    // –ó–ê–©–ò–¢–ê: –ü—Ä–æ–≤–µ—Ä–∫–∞ –≤—Ä–µ–º–µ–Ω–∏ –º–µ–∂–¥—É –æ—Ç–ø—Ä–∞–≤–∫–∞–º–∏ (–º–∏–Ω–∏–º—É–º 30 —Å–µ–∫—É–Ω–¥)
    const now = Date.now();
    const timeSinceLastSubmit = now - lastSubmitTime;
    const minDelay = 30000; // 30 —Å–µ–∫—É–Ω–¥
    
    if (timeSinceLastSubmit < minDelay && lastSubmitTime > 0) {
      const remainingSeconds = Math.ceil((minDelay - timeSinceLastSubmit) / 1000);
      alert(`–ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–¥–æ–∂–¥–∏—Ç–µ ${remainingSeconds} —Å–µ–∫—É–Ω–¥ –ø–µ—Ä–µ–¥ –ø–æ–≤—Ç–æ—Ä–Ω–æ–π –æ—Ç–ø—Ä–∞–≤–∫–æ–π.`);
      return;
    }
    
    // –û—Ç–º–µ—á–∞–µ–º –≤—Å–µ –ø–æ–ª—è –∫–∞–∫ "—Ç—Ä–æ–Ω—É—Ç—ã–µ"
    setTouched({
      name: true,
      email: true,
      message: true
    })
    
    // –í–∞–ª–∏–¥–∞—Ü–∏—è —Ñ–æ—Ä–º—ã
    if (!validateForm()) {
      return // –û—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –æ—Ç–ø—Ä–∞–≤–∫—É –µ—Å–ª–∏ –µ—Å—Ç—å –æ—à–∏–±–∫–∏
    }
    
    // –ó–ê–©–ò–¢–ê: –ü—Ä–æ–≤–µ—Ä–∫–∞ honeypot (–µ—Å–ª–∏ –∑–∞–ø–æ–ª–Ω–µ–Ω–æ - —ç—Ç–æ –±–æ—Ç)
    if (formData.honeypot) {
      // –î–µ–ª–∞–µ–º –≤–∏–¥ —á—Ç–æ –æ—Ç–ø—Ä–∞–≤–∏–ª–∏
      setIsSubmitting(true);
      setTimeout(() => {
        setIsSubmitting(false);
        setIsSubmitted(true);
        setTimeout(() => setIsSubmitted(false), 4000);
      }, 2000);
      return;
    }
    
    setIsSubmitting(true)
    
    try {
      // –û—Ç–ø—Ä–∞–≤–∫–∞ –Ω–∞ backend API
      const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3070'
      const response = await fetch(`${API_URL}/api/contact`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          name: formData.name,
          email: formData.email,
          company: formData.company,
          message: formData.message
          // honeypot –ù–ï –æ—Ç–ø—Ä–∞–≤–ª—è–µ–º
        })
      })
      
      const data = await response.json()
      
      if (!response.ok) {
        throw new Error(data.message || '–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ—Ç–ø—Ä–∞–≤–∫–µ —Å–æ–æ–±—â–µ–Ω–∏—è')
      }
      
      if (data.previewUrl) {
        // –û—Ç–∫—Ä—ã–≤–∞–µ–º –ø–∏—Å—å–º–æ –≤ –Ω–æ–≤–æ–π –≤–∫–ª–∞–¥–∫–µ –¥–ª—è —Ç–µ—Å—Ç–∞
        window.open(data.previewUrl, '_blank')
      }
      
      // –°–æ—Ö—Ä–∞–Ω—è–µ–º –≤—Ä–µ–º—è –æ—Ç–ø—Ä–∞–≤–∫–∏
      setLastSubmitTime(now)
      setIsSubmitted(true)
      
      // –°–±—Ä–æ—Å —Ñ–æ—Ä–º—ã —á–µ—Ä–µ–∑ 4 —Å–µ–∫—É–Ω–¥—ã
      setTimeout(() => {
        setFormData({ name: '', email: '', company: '', message: '', honeypot: '' })
        setIsSubmitted(false)
        setIsPrivacyAccepted(false)
        setErrors({})
        setTouched({})
      }, 4000)
      
    } catch (error) {
      alert('–ù–µ —É–¥–∞–ª–æ—Å—å –æ—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ. –ü–æ–∂–∞–ª—É–π—Å—Ç–∞, –ø–æ–ø—Ä–æ–±—É–π—Ç–µ –ø–æ–∑–∂–µ –∏–ª–∏ —Å–≤—è–∂–∏—Ç–µ—Å—å —Å –Ω–∞–º–∏ –ø–æ —Ç–µ–ª–µ—Ñ–æ–Ω—É.')
    } finally {
      setIsSubmitting(false)
    }
  }

  const handlePrivacyPolicyClick = (e: React.MouseEvent) => {
    e.preventDefault()
    // TODO: –û—Ç–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å –ø–æ–ª–∏—Ç–∏–∫–æ–π –∫–æ–Ω—Ñ–∏–¥–µ–Ω—Ü–∏–∞–ª—å–Ω–æ—Å—Ç–∏
  }

  const handlePersonalDataClick = (e: React.MouseEvent) => {
    e.preventDefault()
    // TODO: –û—Ç–∫—Ä—ã—Ç—å –º–æ–¥–∞–ª—å–Ω–æ–µ –æ–∫–Ω–æ —Å —Å–æ–≥–ª–∞—Å–∏–µ–º –Ω–∞ –æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö
  }

  const contactMethods = [
    {
      icon: Phone,
      title: '–ü–æ–∑–≤–æ–Ω–∏—Ç—å',
      content: '8 (800) 511-12-33',
      description: '–ó–≤–æ–Ω–æ–∫ –ø–æ –†–æ—Å—Å–∏–∏ –±–µ—Å–ø–ª–∞—Ç–Ω—ã–π',
      color: 'from-yellow-400 to-amber-400',
      delay: 0.1
    },
    {
      icon: Mail,
      title: '–ù–∞–ø–∏—Å–∞—Ç—å',
      content: 'info@instrument-fit.ru',
      description: '–û—Ç–≤–µ—Ç–∏–º –≤ —Ç–µ—á–µ–Ω–∏–µ —á–∞—Å–∞',
      color: 'from-yellow-500 to-orange-400',
      delay: 0.2
    },
    {
      icon: MapPin,
      title: '–û—Ñ–∏—Å',
      content: '–ú–æ—Å–∫–≤–∞, –†–æ—Å—Å–∏—è',
      description: '–†–∞–±–æ—Ç–∞–µ–º –ø–æ –≤—Å–µ–π —Å—Ç—Ä–∞–Ω–µ',
      color: 'from-amber-400 to-yellow-500',
      delay: 0.3
    }
  ]

  return (
    <section id="contact" className="contact-section">
      <div className="container">
        {/* –ó–∞–≥–æ–ª–æ–≤–æ–∫ —Å–µ–∫—Ü–∏–∏ */}
        <motion.div 
          className="contact-hero"
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8 }}
          viewport={{ once: true }}
        >
          <motion.div 
            className="hero-badge"
            initial={{ opacity: 0, scale: 0.8 }}
            whileInView={{ opacity: 1, scale: 1 }}
            transition={{ duration: 0.6, delay: 0.2 }}
            viewport={{ once: true }}
          >
            <Sparkles size={16} />
            <span>–ì–æ—Ç–æ–≤—ã –∫ —Å–æ—Ç—Ä—É–¥–Ω–∏—á–µ—Å—Ç–≤—É</span>
          </motion.div>
          
          <h2 className="contact-hero-title">
            –°–≤—è–∑–∞—Ç—å—Å—è —Å –Ω–∞–º–∏ ‚Äî
            <span className="title-highlight"> –ø—Ä–æ—Å—Ç–æ</span>
          </h2>
          
          <p className="contact-hero-description">
            –û–±—Å—É–¥–∏–º –ø–∞—Ä—Ç–Ω–µ—Ä—Å—Ç–≤–æ, –æ—Ç–≤–µ—Ç–∏–º –Ω–∞ –≤–æ–ø—Ä–æ—Å—ã, –ø–æ–º–æ–∂–µ–º —Å –≤—ã–±–æ—Ä–æ–º –æ–±–æ—Ä—É–¥–æ–≤–∞–Ω–∏—è. 
            –ú—ã –≤—Å–µ–≥–¥–∞ –Ω–∞ —Å–≤—è–∑–∏!
          </p>
        </motion.div>

        {/* –û—Å–Ω–æ–≤–Ω–æ–π –∫–æ–Ω—Ç–µ–Ω—Ç - –ø–ª–∞—à–∫–∏ —Å–ª–µ–≤–∞, —Ñ–æ—Ä–º–∞ —Å–ø—Ä–∞–≤–∞ */}
        <div className="contact-main-content">
          {/* –ú–µ—Ç–æ–¥—ã —Å–≤—è–∑–∏ */}
          <div className="contact-methods-sidebar">
            {contactMethods.map((method, index) => (
              <motion.div
                key={index}
                className="contact-method-card"
                initial={{ opacity: 0, x: -50 }}
                whileInView={{ opacity: 1, x: 0 }}
                transition={{ duration: 0.6, delay: method.delay }}
                viewport={{ once: true }}
                whileHover={{ 
                  x: 10, 
                  scale: 1.02,
                  transition: { duration: 0.2 }
                }}
              >
                <div className={`method-icon-container bg-gradient-to-r ${method.color}`}>
                  <method.icon size={24} />
                </div>
                
                <div className="method-content">
                  <h3 className="method-title">{method.title}</h3>
                  <p className="method-main">{method.content}</p>
                  <p className="method-description">{method.description}</p>
                </div>
                
                <div className="method-hover-effect"></div>
              </motion.div>
            ))}
          </div>

          {/* –§–æ—Ä–º–∞ —Å–≤—è–∑–∏ */}
          <motion.div 
            className="contact-form-section"
            initial={{ opacity: 0, x: 50 }}
            whileInView={{ opacity: 1, x: 0 }}
            transition={{ duration: 0.8, delay: 0.4 }}
            viewport={{ once: true }}
          >
            <div className="form-container">
              <div className="form-header">
                <h3>–û—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ</h3>
                <p>–ó–∞–ø–æ–ª–Ω–∏—Ç–µ —Ñ–æ—Ä–º—É, –∏ –º—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è</p>
              </div>
              
              {isSubmitted ? (
                <motion.div 
                  className="success-message"
                  initial={{ opacity: 0, scale: 0.8 }}
                  animate={{ opacity: 1, scale: 1 }}
                  transition={{ duration: 0.5 }}
                >
                  <motion.div
                    className="success-icon"
                    animate={{ scale: [1, 1.2, 1] }}
                    transition={{ duration: 0.6, repeat: 2 }}
                  >
                    ‚ú®
                  </motion.div>
                  <h4>–°–æ–æ–±—â–µ–Ω–∏–µ –æ—Ç–ø—Ä–∞–≤–ª–µ–Ω–æ!</h4>
                  <p>–°–ø–∞—Å–∏–±–æ –∑–∞ –≤–∞—à –∏–Ω—Ç–µ—Ä–µ—Å. –ú—ã —Å–≤—è–∂–µ–º—Å—è —Å –≤–∞–º–∏ –≤ –±–ª–∏–∂–∞–π—à–µ–µ –≤—Ä–µ–º—è.</p>
                </motion.div>
              ) : (
                <form onSubmit={handleSubmit} className="modern-form">
                  <div className="form-row">
                    <div className={`floating-input-group ${errors.name && touched.name ? 'error' : ''}`}>
                      <input
                        type="text"
                        name="name"
                        value={formData.name}
                        onChange={handleChange}
                        onFocus={() => setFocusedField('name')}
                        onBlur={handleBlur}
                        className={focusedField === 'name' || formData.name ? 'focused' : ''}
                      />
                      <label>
                        <User size={18} />
                        –í–∞—à–µ –∏–º—è *
                      </label>
                      <div className="input-border"></div>
                      {errors.name && touched.name && (
                        <span className="error-message">{errors.name}</span>
                      )}
                    </div>

                    <div className={`floating-input-group ${errors.email && touched.email ? 'error' : ''}`}>
                      <input
                        type="email"
                        name="email"
                        value={formData.email}
                        onChange={handleChange}
                        onFocus={() => setFocusedField('email')}
                        onBlur={handleBlur}
                        className={focusedField === 'email' || formData.email ? 'focused' : ''}
                      />
                      <label>
                        <Mail size={18} />
                        Email –∞–¥—Ä–µ—Å *
                      </label>
                      <div className="input-border"></div>
                      {errors.email && touched.email && (
                        <span className="error-message">{errors.email}</span>
                      )}
                    </div>
                  </div>

                  {/* –°–∫—Ä—ã—Ç–æ–µ honeypot –ø–æ–ª–µ –¥–ª—è –∑–∞—â–∏—Ç—ã –æ—Ç –±–æ—Ç–æ–≤ */}
                  <input
                    type="text"
                    name="honeypot"
                    value={formData.honeypot}
                    onChange={handleChange}
                    autoComplete="off"
                    tabIndex={-1}
                    style={{
                      position: 'absolute',
                      left: '-9999px',
                      width: '1px',
                      height: '1px',
                      opacity: 0,
                      pointerEvents: 'none'
                    }}
                    aria-hidden="true"
                  />

                  <div className="floating-input-group">
                    <input
                      type="text"
                      name="company"
                      value={formData.company}
                      onChange={handleChange}
                      onFocus={() => setFocusedField('company')}
                      onBlur={() => setFocusedField(null)}
                      className={focusedField === 'company' || formData.company ? 'focused' : ''}
                    />
                    <label>
                      <Building size={18} />
                      –ö–æ–º–ø–∞–Ω–∏—è (–Ω–µ–æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ)
                    </label>
                    <div className="input-border"></div>
                  </div>

                  <div className={`floating-input-group ${errors.message && touched.message ? 'error' : ''}`}>
                    <textarea
                      name="message"
                      value={formData.message}
                      onChange={handleChange}
                      onFocus={() => setFocusedField('message')}
                      onBlur={handleBlur}
                      rows={4}
                      className={focusedField === 'message' || formData.message ? 'focused' : ''}
                    />
                    <label>
                      <MessageSquare size={18} />
                      –í–∞—à–µ —Å–æ–æ–±—â–µ–Ω–∏–µ *
                    </label>
                    <div className="input-border"></div>
                    {errors.message && touched.message && (
                      <span className="error-message">{errors.message}</span>
                    )}
                  </div>

                  <div className={`privacy-consent ${errors.privacy ? 'error' : ''}`}>
                    <label className="privacy-checkbox-label">
                      <input
                        type="checkbox"
                        checked={isPrivacyAccepted}
                        onChange={(e) => {
                          setIsPrivacyAccepted(e.target.checked)
                          if (e.target.checked) {
                            setErrors(prev => ({ ...prev, privacy: undefined }))
                          }
                        }}
                        className="privacy-checkbox"
                      />
                      <span className="checkbox-custom"></span>
                      <span className="privacy-text">
                        –Ø –æ–∑–Ω–∞–∫–æ–º–ª–µ–Ω —Å{'\u00A0'}
                        <button type="button" onClick={handlePrivacyPolicyClick} className="privacy-link">
                          ¬´–ü–æ–ª–∏—Ç–∏–∫–æ–π –≤{'\u00A0'}–æ—Ç–Ω–æ—à–µ–Ω–∏–∏ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö¬ª
                        </button>
                        {'\u00A0'}–∏ –ø—Ä–µ–¥–æ—Å—Ç–∞–≤–ª—è—é{'\u00A0'}
                        <button type="button" onClick={handlePersonalDataClick} className="privacy-link">
                          ¬´–°–æ–≥–ª–∞—Å–∏–µ –Ω–∞{'\u00A0'}–æ–±—Ä–∞–±–æ—Ç–∫—É –ø–µ—Ä—Å–æ–Ω–∞–ª—å–Ω—ã—Ö –¥–∞–Ω–Ω—ã—Ö¬ª
                        </button>
                      </span>
                    </label>
                    {errors.privacy && (
                      <span className="error-message">{errors.privacy}</span>
                    )}
                  </div>

                  <motion.button 
                    type="submit" 
                    className="submit-modern-btn"
                    disabled={isSubmitting}
                    whileHover={{ scale: !isSubmitting ? 1.02 : 1 }}
                    whileTap={{ scale: !isSubmitting ? 0.98 : 1 }}
                  >
                    <span className="btn-content">
                      {isSubmitting ? (
                        <>
                          <motion.div 
                            className="btn-spinner"
                            animate={{ rotate: 360 }}
                            transition={{ duration: 1, repeat: Infinity, ease: "linear" }}
                          />
                          –û—Ç–ø—Ä–∞–≤–ª—è–µ–º...
                        </>
                      ) : (
                        <>
                          <Send size={20} />
                          –û—Ç–ø—Ä–∞–≤–∏—Ç—å —Å–æ–æ–±—â–µ–Ω–∏–µ
                        </>
                      )}
                    </span>
                    <div className="btn-glow"></div>
                  </motion.button>
                </form>
              )}
            </div>
          </motion.div>
        </div>
      </div>
    </section>
  )
}

export default Contact 