/**
 * –õ–µ–Ω–∏–≤–∞—è –∑–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–π –¥–ª—è –æ–ø—Ç–∏–º–∏–∑–∞—Ü–∏–∏ –ø—Ä–æ–∏–∑–≤–æ–¥–∏—Ç–µ–ª—å–Ω–æ—Å—Ç–∏
 */

export class LazyLoader {
  private observer: IntersectionObserver | null = null;
  private images: Set<HTMLImageElement> = new Set();

  constructor() {
    this.initObserver();
  }

  /**
   * –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è IntersectionObserver
   */
  private initObserver(): void {
    if ('IntersectionObserver' in window) {
      this.observer = new IntersectionObserver(
        (entries) => {
          entries.forEach((entry) => {
            if (entry.isIntersecting) {
              const img = entry.target as HTMLImageElement;
              this.loadImage(img);
            }
          });
        },
        {
          rootMargin: '500px', // –ù–∞—á–∏–Ω–∞–µ–º –∑–∞–≥—Ä—É–∑–∫—É –∑–∞ 500px –¥–æ –ø–æ—è–≤–ª–µ–Ω–∏—è (–±—ã–ª–æ 50px)
          threshold: 0.01,
        }
      );
    }
  }

  /**
   * –ó–∞–≥—Ä—É–∑–∫–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è
   */
  private loadImage(img: HTMLImageElement): void {
    const src = img.dataset.src;
    if (!src) return;

    img.src = src;
    img.removeAttribute('data-src');
    
    img.onload = () => {
      img.classList.add('loaded');
    };

    if (this.observer) {
      this.observer.unobserve(img);
    }
    
    this.images.delete(img);
  }

  /**
   * –ù–∞–±–ª—é–¥–µ–Ω–∏–µ –∑–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏–µ–º
   */
  public observe(img: HTMLImageElement): void {
    if (!this.observer) {
      // Fallback –¥–ª—è —Å—Ç–∞—Ä—ã—Ö –±—Ä–∞—É–∑–µ—Ä–æ–≤ - –∑–∞–≥—Ä—É–∂–∞–µ–º —Å—Ä–∞–∑—É
      this.loadImage(img);
      return;
    }

    this.images.add(img);
    this.observer.observe(img);
  }

  /**
   * –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–æ–µ –Ω–∞–±–ª—é–¥–µ–Ω–∏–µ –∑–∞ –≤—Å–µ–º–∏ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è–º–∏ —Å data-src
   */
  public observeAll(): void {
    const images = document.querySelectorAll('img[data-src]');
    images.forEach((img) => {
      this.observe(img as HTMLImageElement);
    });
  }

  /**
   * –û—á–∏—Å—Ç–∫–∞
   */
  public destroy(): void {
    if (this.observer) {
      this.observer.disconnect();
    }
    this.images.clear();
  }
}

// –≠–∫—Å–ø–æ—Ä—Ç–∏—Ä—É–µ–º singleton
export const lazyLoader = new LazyLoader();

// –ê–≤—Ç–æ–º–∞—Ç–∏—á–µ—Å–∫–∏ –Ω–∞–±–ª—é–¥–∞–µ–º –∑–∞ –∏–∑–æ–±—Ä–∞–∂–µ–Ω–∏—è–º–∏ –ø–æ—Å–ª–µ –∑–∞–≥—Ä—É–∑–∫–∏ DOM
if (typeof window !== 'undefined') {
  window.addEventListener('DOMContentLoaded', () => {
    lazyLoader.observeAll();
    
    // –ü–µ—Ä–µ–æ–±—Å–µ—Ä–≤–∏–º –ø–æ—Å–ª–µ –∏–∑–º–µ–Ω–µ–Ω–∏–π DOM (–Ω–∞–ø—Ä–∏–º–µ—Ä, –ø—Ä–∏ —Å–º–µ–Ω–µ —Å–ª–∞–π–¥–æ–≤)
    const observeNewImages = () => {
      setTimeout(() => lazyLoader.observeAll(), 100);
    };
    
    // –°–ª—É—à–∞–µ–º –∫–∞—Å—Ç–æ–º–Ω—ã–µ —Å–æ–±—ã—Ç–∏—è –æ—Ç –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–æ–≤
    window.addEventListener('imagesLoaded', observeNewImages);
  });
}

