import { useState, useEffect, useCallback } from 'react'
import { Menu, X } from 'lucide-react'
import { getAssetPath } from '../../utils/paths'
import { scrollController } from '../../utils/scrollController'
import './Header.css'

const Header = () => {
  const [isMenuOpen, setIsMenuOpen] = useState(false)
  const [isScrolled, setIsScrolled] = useState(false)
  const [activeSection, setActiveSection] = useState('home')

  // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ —Å–∫—Ä–æ–ª–ª–∞ –¥–ª—è –∏–∑–º–µ–Ω–µ–Ω–∏—è —Å—Ç–∏–ª–µ–π —Ö–µ–¥–µ—Ä–∞
  useEffect(() => {
    const handleScroll = () => {
      const scrollTop = window.scrollY
      setIsScrolled(scrollTop > 50)
    }

    window.addEventListener('scroll', handleScroll, { passive: true })
    return () => window.removeEventListener('scroll', handleScroll)
  }, [])

  // –§—É–Ω–∫—Ü–∏—è –¥–ª—è –æ–ø—Ä–µ–¥–µ–ª–µ–Ω–∏—è –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ–∫—Ü–∏–∏ —Å —É–ª—É—á—à–µ–Ω–Ω–æ–π –ª–æ–≥–∏–∫–æ–π
  const updateActiveSection = useCallback(() => {
    const sections = ['home', 'stats', 'about', 'brands', 'timeline', 'map', 'services', 'contact']
    const scrollPosition = window.scrollY
    const windowHeight = window.innerHeight
    
    // –ù–∞—Ö–æ–¥–∏–º —Å–µ–∫—Ü–∏—é –∫–æ—Ç–æ—Ä–∞—è –±–æ–ª—å—à–µ –≤—Å–µ–≥–æ –≤–∏–¥–Ω–∞ –Ω–∞ —ç–∫—Ä–∞–Ω–µ
    let currentSection = 'home'
    let maxVisibility = 0
    
    sections.forEach(sectionId => {
      const element = document.getElementById(sectionId)
      if (!element) return
      
      const rect = element.getBoundingClientRect()
      const headerHeight = 80
      
      // –ö–æ—Ä—Ä–µ–∫—Ç–∏—Ä—É–µ–º –ø–æ–∑–∏—Ü–∏—é —Å —É—á–µ—Ç–æ–º —Ö–µ–¥–µ—Ä–∞
      const top = rect.top + scrollPosition - headerHeight
      const bottom = rect.bottom + scrollPosition - headerHeight
      
      // –û–ø—Ä–µ–¥–µ–ª—è–µ–º –≤–∏–¥–∏–º–æ—Å—Ç—å —Å–µ–∫—Ü–∏–∏
      let visibility = 0
      if (top <= scrollPosition + headerHeight && bottom >= scrollPosition + headerHeight) {
        const visibleTop = Math.max(scrollPosition + headerHeight, top)
        const visibleBottom = Math.min(scrollPosition + windowHeight, bottom)
        visibility = (visibleBottom - visibleTop) / windowHeight
      }
      
      if (visibility > maxVisibility) {
        maxVisibility = visibility
        currentSection = sectionId
      }
    })
    
    setActiveSection(currentSection)
  }, [])

  // –û—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏–µ –∞–∫—Ç–∏–≤–Ω–æ–π —Å–µ–∫—Ü–∏–∏ —Å debounce
  useEffect(() => {
    let timeoutId: NodeJS.Timeout
    
    const handleScroll = () => {
      clearTimeout(timeoutId)
      timeoutId = setTimeout(updateActiveSection, 50)
    }

    window.addEventListener('scroll', handleScroll, { passive: true })
    updateActiveSection() // –í—ã–∑—ã–≤–∞–µ–º —Å—Ä–∞–∑—É –¥–ª—è —É—Å—Ç–∞–Ω–æ–≤–∫–∏ –Ω–∞—á–∞–ª—å–Ω–æ–≥–æ —Å–æ—Å—Ç–æ—è–Ω–∏—è
    
    return () => {
      window.removeEventListener('scroll', handleScroll)
      clearTimeout(timeoutId)
    }
  }, [updateActiveSection])

  // –£–ª—É—á—à–µ–Ω–Ω–∞—è –ø–ª–∞–≤–Ω–∞—è –ø—Ä–æ–∫—Ä—É—Ç–∫–∞ –∫ —Å–µ–∫—Ü–∏–∏ —Å –∏—Å–ø–æ–ª—å–∑–æ–≤–∞–Ω–∏–µ–º scrollController
  const handleNavClick = useCallback((href: string) => {
    const targetId = href.replace('#', '')
    const targetElement = document.getElementById(targetId)
    
    if (!targetElement) {
      console.warn(`Element with id "${targetId}" not found`)
      return
    }
    
    // –ó–∞–∫—Ä—ã–≤–∞–µ–º –º–æ–±–∏–ª—å–Ω–æ–µ –º–µ–Ω—é —Å—Ä–∞–∑—É
    setIsMenuOpen(false)
    
    // –ö–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã–µ —Å–µ–∫—Ü–∏–∏ (—É–ø—Ä–∞–≤–ª—è—é—Ç—Å—è —á–µ—Ä–µ–∑ scrollController)
    const controlledSections = ['home', 'about', 'brands', 'timeline']
    const sectionIndex = controlledSections.indexOf(targetId)
    
    if (sectionIndex >= 0) {
      // –ò—Å–ø–æ–ª—å–∑—É–µ–º scrollController –¥–ª—è –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π
      scrollController.scrollToSection(sectionIndex, true)
    } else {
      // –î–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Å–µ–∫—Ü–∏–π –∏—Å–ø–æ–ª—å–∑—É–µ–º —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω—ã–π scrollIntoView —Å —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ–º
      // –ò –æ—Ç–∫–ª—é—á–∞–µ–º scrollController
      scrollController.leaveControlledArea()
      
      targetElement.scrollIntoView({
        behavior: 'smooth',
        block: 'center',
        inline: 'nearest'
      })
    }
    
    // –ü–æ—Å–ª–µ –∑–∞–≤–µ—Ä—à–∞–Ω–∏—è –∞–Ω–∏–º–∞—Ü–∏–∏ –æ–±–Ω–æ–≤–ª—è–µ–º –∞–∫—Ç–∏–≤–Ω—É—é —Å–µ–∫—Ü–∏—é
    setTimeout(updateActiveSection, 1000)
  }, [updateActiveSection])

  const menuItems = [
    { href: '#home', label: '–ì–ª–∞–≤–Ω–∞—è', id: 'home' },
    { href: '#about', label: '–û –∫–æ–º–ø–∞–Ω–∏–∏', id: 'about' },
    { href: '#brands', label: '–ë—Ä–µ–Ω–¥—ã', id: 'brands' },
    { href: '#timeline', label: '–ò—Å—Ç–æ—Ä–∏—è', id: 'timeline' },
    { href: '#map', label: '–ì–µ–æ–≥—Ä–∞—Ñ–∏—è', id: 'map' },
    { href: '#services', label: '–£—Å–ª—É–≥–∏', id: 'services' },
    { href: '#contact', label: '–ö–æ–Ω—Ç–∞–∫—Ç—ã', id: 'contact' }
  ]

  return (
    <header className={`header ${isScrolled ? 'scrolled' : ''}`}>
      <div className="container">
        <div className="header-content">
          {/* –õ–æ–≥–æ—Ç–∏–ø */}
          <div className="logo">
            <img src={getAssetPath("logo/fit-logo-clean.svg")} alt="FIT" className="logo-img" />
          </div>

          {/* –ù–∞–≤–∏–≥–∞—Ü–∏—è –¥–ª—è –¥–µ—Å–∫—Ç–æ–ø–∞ */}
          <nav className="nav-desktop">
            {menuItems.map((item) => (
              <a 
                key={item.href}
                href={item.href} 
                className={`nav-link ${activeSection === item.id ? 'active' : ''}`}
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  handleNavClick(item.href)
                }}
              >
                {item.label}
              </a>
            ))}
          </nav>

          {/* –ö–Ω–æ–ø–∫–∞ –∫–∞—Ç–∞–ª–æ–≥–∞ –¥–ª—è –¥–µ—Å–∫—Ç–æ–ø–∞ */}
          <a 
            href="https://fit24.ru/" 
            target="_blank" 
            rel="noopener noreferrer"
            className="catalog-button"
          >
            –ö–∞—Ç–∞–ª–æ–≥
          </a>

          {/* –ö–Ω–æ–ø–∫–∞ –º–æ–±–∏–ª—å–Ω–æ–≥–æ –º–µ–Ω—é */}
          <button 
            className="mobile-menu-toggle"
            onClick={() => setIsMenuOpen(!isMenuOpen)}
            aria-label="–û—Ç–∫—Ä—ã—Ç—å –º–µ–Ω—é"
          >
            {isMenuOpen ? <X size={24} /> : <Menu size={24} />}
          </button>
        </div>

        {/* –ú–æ–±–∏–ª—å–Ω–∞—è –Ω–∞–≤–∏–≥–∞—Ü–∏—è */}
        {isMenuOpen && (
          <nav className="nav-mobile">
            {menuItems.map((item) => (
              <a 
                key={item.href}
                href={item.href} 
                className={`nav-link-mobile ${activeSection === item.id ? 'active' : ''}`}
                onClick={(e) => {
                  e.preventDefault()
                  e.stopPropagation()
                  handleNavClick(item.href)
                }}
              >
                {item.label}
              </a>
            ))}
            
            {/* –ö–Ω–æ–ø–∫–∞ –∫–∞—Ç–∞–ª–æ–≥–∞ –≤ –º–æ–±–∏–ª—å–Ω–æ–º –º–µ–Ω—é */}
            <a 
              href="https://fit24.ru/" 
              target="_blank" 
              rel="noopener noreferrer"
              className="catalog-button-mobile"
              onClick={() => setIsMenuOpen(false)}
            >
              –ö–∞—Ç–∞–ª–æ–≥
            </a>
          </nav>
        )}
      </div>
    </header>
  )
}

export default Header 