import { useEffect, useRef } from 'react'
import Header from '../components/Header/Header'
import Hero from '../components/Hero/Hero'
import About from '../components/About/About'
import Brands from '../components/Brands/Brands'
import Timeline from '../components/Timeline/Timeline'
import Services from '../components/Services/Services'
import RussiaMap from '../components/RussiaMap/RussiaMap'
import Contact from '../components/Contact/Contact'
import Footer from '../components/Footer/Footer'
import { scrollController } from '../utils/scrollController'
import './HomePage.css'

const HomePage = () => {
  const isInitialized = useRef(false)

  useEffect(() => {
    // –ü—Ä–µ–¥–æ—Ç–≤—Ä–∞—â–∞–µ–º –ø–æ–≤—Ç–æ—Ä–Ω—É—é –∏–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—é
    if (isInitialized.current) return
    isInitialized.current = true

    // –í–æ—Å—Å—Ç–∞–Ω–∞–≤–ª–∏–≤–∞–µ–º –ø–æ–ª–æ–∂–µ–Ω–∏–µ —Å—Ç—Ä–∞–Ω–∏—Ü—ã –Ω–∞ –≤–µ—Ä—Ö –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ
    window.scrollTo(0, 0)

    // –ù–µ–±–æ–ª—å—à–∞—è –∑–∞–¥–µ—Ä–∂–∫–∞ –¥–ª—è –∑–∞–≥—Ä—É–∑–∫–∏ DOM
    const initTimeout = setTimeout(() => {
      // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∏—Ä—É–µ–º ScrollController —Ç–æ–ª—å–∫–æ –¥–ª—è –ø–µ—Ä–≤—ã—Ö —Å–µ–∫—Ü–∏–π (–¥–æ Timeline –≤–∫–ª—é—á–∏—Ç–µ–ª—å–Ω–æ)
      // –ü–æ—Å–ª–µ Timeline - –æ–±—ã—á–Ω—ã–π –ø–ª–∞–≤–Ω—ã–π —Å–∫—Ä–æ–ª–ª
      const sections = ['home', 'about', 'brands', 'timeline']
      scrollController.init(sections)

      // –ö–æ–ª–±—ç–∫ –¥–ª—è –æ—Ç—Å–ª–µ–∂–∏–≤–∞–Ω–∏—è —Å–º–µ–Ω—ã —Å–µ–∫—Ü–∏–π
      scrollController.onSectionChangeCallback((index, section) => {
        console.log(`–ü–µ—Ä–µ—Ö–æ–¥ –∫ —Å–µ–∫—Ü–∏–∏: ${section.id}, –∏–Ω–¥–µ–∫—Å: ${index}`)
      })

      // –ö–æ–ª–±—ç–∫ –¥–ª—è –ø—Ä–æ–≥—Ä–µ—Å—Å–∞ –≥–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω–æ–≥–æ —Å–∫—Ä–æ–ª–ª–∞
      scrollController.onHorizontalProgressCallback((progress, max) => {
        console.log(`–ì–æ—Ä–∏–∑–æ–Ω—Ç–∞–ª—å–Ω—ã–π —Å–∫—Ä–æ–ª–ª: ${progress}/${max}`)
      })
    }, 300)

    // –û–±—Ä–∞–±–æ—Ç–∫–∞ –Ω–∞–≤–∏–≥–∞—Ü–∏–∏ –ø–æ —Ö—ç—à—É –≤ URL
    const handleHashChange = () => {
      if (window.location.hash) {
        const sectionId = window.location.hash.substring(1)
        const element = document.getElementById(sectionId)
        if (element) {
          const controlledSections = ['home', 'about', 'brands', 'timeline']
          const index = controlledSections.indexOf(sectionId)
          if (index >= 0) {
            // –ò—Å–ø–æ–ª—å–∑—É–µ–º ScrollController –¥–ª—è –∫–æ–Ω—Ç—Ä–æ–ª–∏—Ä—É–µ–º—ã—Ö —Å–µ–∫—Ü–∏–π
            scrollController.scrollToSection(index, true)
          } else {
            // –î–ª—è –æ—Å—Ç–∞–ª—å–Ω—ã—Ö —Å–µ–∫—Ü–∏–π - –æ–±—ã—á–Ω—ã–π smooth scroll —Å —Ü–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ–º
            element.scrollIntoView({ behavior: 'smooth', block: 'center' })
          }
        }
      }
    }

    // –û–±—Ä–∞–±–æ—Ç–∫–∞ –∏–∑–Ω–∞—á–∞–ª—å–Ω–æ–≥–æ —Ö—ç—à–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ
    if (window.location.hash) {
      setTimeout(() => {
        handleHashChange()
      }, 500)
    }

    window.addEventListener('hashchange', handleHashChange)

    // Cleanup
    return () => {
      clearTimeout(initTimeout)
      window.removeEventListener('hashchange', handleHashChange)
      scrollController.destroy()
      isInitialized.current = false
    }
  }, [])

  return (
    <div className="homepage">
      <Header />
      <Hero />
      <About />
      <Brands />
      <Timeline />
      <RussiaMap />
      <Services />
      <Contact />
      <Footer />
    </div>
  )
}

export default HomePage 