import { useEffect, useRef } from 'react'
import { getAssetPath } from '../../utils/paths'
import { performanceOptimizer } from '../../utils/performanceOptimizer'
import './Hero.css'

const Hero = () => {
  const statsRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    // –ò–Ω–∏—Ü–∏–∞–ª–∏–∑–∞—Ü–∏—è —á–∞—Å—Ç–∏—Ü
    createHeroParticles()
  }, [])

  useEffect(() => {
    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          if (entry.isIntersecting) {
            const counters = entry.target.querySelectorAll('.stat-number')
            counters.forEach((counter) => {
              animateCounter(counter as HTMLElement)
            })
          }
        })
      },
      { threshold: 0.5 }
    )

    if (statsRef.current) {
      observer.observe(statsRef.current)
    }

    return () => observer.disconnect()
  }, [])

  const animateCounter = (element: HTMLElement) => {
    const target = parseInt(element.getAttribute('data-count') || '0')
    const suffix = element.getAttribute('data-suffix') || ''
    
    // –ù–∞ —Å–ª–∞–±—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö –ø–æ–∫–∞–∑—ã–≤–∞–µ–º —á–∏—Å–ª–æ —Å—Ä–∞–∑—É –±–µ–∑ –∞–Ω–∏–º–∞—Ü–∏–∏
    if (performanceOptimizer.isLowEnd()) {
      element.textContent = target.toString() + suffix
      return
    }
    
    const duration = 2000
    const start = 0
    const startTime = performance.now()

    const updateCounter = (currentTime: number) => {
      const elapsed = currentTime - startTime
      const progress = Math.min(elapsed / duration, 1)
      
      const easeOutQuart = 1 - Math.pow(1 - progress, 4)
      const current = Math.floor(start + (target - start) * easeOutQuart)
      
      element.textContent = current.toString() + suffix
      
      if (progress < 1) {
        requestAnimationFrame(updateCounter)
      } else {
        element.textContent = target.toString() + suffix
      }
    }

    requestAnimationFrame(updateCounter)
  }

  const createHeroParticles = () => {
    // –ù–∞ —Å–ª–∞–±—ã—Ö —É—Å—Ç—Ä–æ–π—Å—Ç–≤–∞—Ö –Ω–µ —Å–æ–∑–¥–∞–µ–º —á–∞—Å—Ç–∏—Ü—ã
    if (performanceOptimizer.isLowEnd()) {
      return
    }
    
    const particlesContainer = document.getElementById('hero-particles')
    if (!particlesContainer) return

    for (let i = 0; i < 30; i++) {
      const particle = document.createElement('div')
      particle.className = 'particle'
      particle.style.left = Math.random() * 100 + '%'
      particle.style.animationDelay = Math.random() * 3 + 's'
      particle.style.animationDuration = (Math.random() * 2 + 2) + 's'
      particlesContainer.appendChild(particle)
    }
  }

  const scrollToBrands = () => {
    document.getElementById('brands')?.scrollIntoView({ behavior: 'smooth' })
  }

  const scrollToContact = () => {
    document.getElementById('contact')?.scrollIntoView({ behavior: 'smooth' })
  }

  return (
    <section className="hero" id="home">
      <div className="hero-background" style={{ backgroundImage: `url('${getAssetPath("img/hero-background.jpg")}')` }}></div>
      <div className="hero-particles" id="hero-particles"></div>
      
      <div className="container">
        <div className="hero-wrapper">
          <div className="hero-content">
            <h1 className="hero-title">–¢–µ—Ö–Ω–æ–ª–æ–≥–∏—á–Ω—ã–π –∏–Ω—Å—Ç—Ä—É–º–µ–Ω—Ç –¥–ª—è –ø—Ä–æ—Ñ–µ—Å—Å–∏–æ–Ω–∞–ª–æ–≤ –∏ –¥–æ–º–∞—à–Ω–∏—Ö –º–∞—Å—Ç–µ—Ä–æ–≤. <br /> <br /> –ì–æ—Ç–æ–≤—ã–µ —Ä–µ—à–µ–Ω–∏—è –¥–ª—è —ç—Ñ—Ñ–µ–∫—Ç–∏–≤–Ω–æ–π —Ä–∞–±–æ—Ç—ã. –ù–∞–¥–µ–∂–Ω—ã–π —Ä–µ–∑—É–ª—å—Ç–∞—Ç  —Å –ø–µ—Ä–≤–æ–≥–æ –ø–æ–¥—Ö–æ–¥–∞!</h1>
            <p className="hero-description">
            <strong>–ë–æ–ª–µ–µ 25 –ª–µ—Ç</strong> –º—ã —Å–æ–∑–¥–∞–µ–º —Ä–µ—à–µ–Ω–∏—è, –∫–æ—Ç–æ—Ä—ã–º <strong>–¥–æ–≤–µ—Ä—è—é—Ç</strong>.
            –ö–æ–º–ø–∞–Ω–∏—è FIT –ø—Ä–µ–¥–ª–∞–≥–∞–µ—Ç <strong>–∫–æ–º–ø–ª–µ–∫—Å–Ω—ã–µ —Ä–µ—à–µ–Ω–∏—è</strong> –¥–ª—è –ª—é–±—ã—Ö –∑–∞–¥–∞—á ‚Äî –æ—Ç –¥–æ–º–∞—à–Ω–µ–≥–æ —Ä–µ–º–æ–Ω—Ç–∞ –¥–æ —Å–ª–æ–∂–Ω—ã—Ö –ø—Ä–æ–º—ã—à–ª–µ–Ω–Ω—ã—Ö –ø—Ä–æ–µ–∫—Ç–æ–≤. –ú—ã –æ–±–µ—Å–ø–µ—á–∏–≤–∞–µ–º –Ω–∞–¥–µ–∂–Ω–æ—Å—Ç—å –∏ —Ä–µ–∑—É–ª—å—Ç–∞—Ç, –Ω–∞ –∫–æ—Ç–æ—Ä—ã–µ –º–æ–∂–Ω–æ –ø–æ–ª–æ–∂–∏—Ç—å—Å—è.
            </p>
            <div className="hero-buttons">
              <button className="btn btn-primary" onClick={scrollToBrands}>
                –ù–∞—à–∏ –±—Ä–µ–Ω–¥—ã
              </button>
              <button className="btn btn-outline" onClick={scrollToContact}>
                –°–≤—è–∑–∞—Ç—å—Å—è —Å –Ω–∞–º–∏
              </button>
            </div>
          </div>
          <div className="hero-image animate fadeInRight">
            <img src={getAssetPath("img/c98be543-3cca-4e81-b5be-98b263057ff3.webp")} alt="–ò–Ω—Å—Ç—Ä—É–º–µ–Ω—Ç—ã FIT" className="hero-img" loading="eager" />
            <div className="hero-glow-overlay"></div>
          </div>
        </div>

        {/* Integrated Stats Section */}
        <div className="hero-stats" ref={statsRef}>
          <div className="stats-grid">
            <a href="https://fit24.ru/catalog/" target="_blank" rel="noopener noreferrer" className="stat-item animate fadeInUp delay-1">
              <div className="stat-number" data-count="12000">0</div>
              <div className="stat-label">–ø–æ–∑–∏—Ü–∏–π –≤ –∫–∞—Ç–∞–ª–æ–≥–µ</div>
            </a>
            <a href="#map" className="stat-item animate fadeInUp delay-2">
              <div className="stat-number" data-count="10">0</div>
              <div className="stat-label">—Ñ–∏–ª–∏–∞–ª–æ–≤ –ø–æ –≤—Å–µ–π –†–æ—Å—Å–∏–∏</div>
            </a>
            <div className="stat-item animate fadeInUp delay-3">
              <div className="stat-number" data-count="1000" data-suffix="+">0</div>
              <div className="stat-label">—Å–æ—Ç—Ä—É–¥–Ω–∏–∫–æ–≤ –≤ —à—Ç–∞—Ç–µ</div>
            </div>
            <div className="stat-item animate fadeInUp delay-4">
              <div className="stat-number" data-count="60" data-suffix="+">0</div>
              <div className="stat-label">–µ–¥–∏–Ω–∏—Ü —Å–æ–±—Å—Ç–≤–µ–Ω–Ω–æ–≥–æ —Ç—Ä–∞–Ω—Å–ø–æ—Ä—Ç–∞</div>
            </div>
          </div>
        </div>
      </div>
      <div className="lava-blobs"></div>
    </section>
  )
}

export default Hero 